import React from 'react'
import logo from '../assets/images/website-icon.png'
import { Link } from 'gatsby'

const HeaderGeneric = props => (
  <header id="header">
    <Link to="/" className="logo">
      <img src={logo} alt="Big R" />
    </Link>
    <h1>props.title</h1>
    <p>props.subheader</p>
  </header>
)

export default HeaderGeneric
